<template>
<div class="page">
  <Header>
    <Title>Button</Title>
    <header-button v-back-link></header-button>
  </Header>
  <page-content>
    <p><m-button> default</m-button></p>
    <p><m-button size="large"> large</m-button></p>
    <p><m-button size="small"> small</m-button></p>
    <p><m-button type="danger"> danger default</m-button></p>
    <p><m-button type="danger" size="large"> danger large</m-button></p>
    <p><m-button type="danger" size="small"> danger small</m-button></p>
    <p><m-button type="light"> light default</m-button></p>
    <p><m-button type="light" size="large"> light large</m-button></p>
    <p><m-button type="light" size="small"> light small</m-button></p>
    <p><m-button type="light" :round="true"> light default</m-button></p>
    <p><m-button type="light" :round="true" size="large"> light large</m-button></p>
    <p><m-button type="light" :round="true" size="small"> light small</m-button></p>
    <p><m-button :round="true"> round default</m-button></p>
    <p><m-button :round="true" size="large"> round default</m-button></p>
    <p><m-button :round="true" size="small"> round default</m-button></p>
    <p><m-button :round="true" :disabled="true"> round default</m-button></p>
    <p><m-button :round="true" size="large" :disabled="true"> round default</m-button></p>
    <p><m-button size="small" :disabled="true"> round default</m-button></p>


  </page-content>
</div>
</template>
<script>
import {Button} from '@/components/button'

import {
  Header,
  hButton,
  Title
} from '@/components/header'

import Content from '@/components/content'

export default {
  components: {
    Header,
    Title,
    'page-content': Content,
    'header-button': hButton,
    'm-button': Button
  },
  name: 'Button',
  data: () => ({

  })
}
</script>
<style lang="less" scoped>
.page-content {
    font-size: 12px;
    padding: 10px;
    p {
      padding: 5px;
    }
}
</style>



