<template>
<div class="page">
  <Header>
    <Title>Toast</Title>
    <header-button v-back-link></header-button>
  </Header>
  <page-content>
    <!--
    <p>
      <m-button @click.stop.native="openToast">Toast</m-button>
    </p>
    -->
    <p>
      <m-button @click.stop.native="openToast('tsuccess')"> Toast </m-button>
    </p>
    <p>
      <m-button @click.stop.native="openToast('tcannel')"> Toast </m-button>
    </p>
    <p>
      <m-button @click.stop.native="openToast('twarn')"> Toast </m-button>
    </p>
    <p>
      <m-button @click.stop.native="openToast('ttext')"> Toast </m-button>
    </p>
    <p>
      <m-button @click.stop.native="openToast('tloading')"> loading</m-button>
    </p>
<!--
    <toast ref="toast" type="xiujia" text=" "></toast>
-->
    <Toast ref="tsuccess" :show="isshow" content="" cls="t-success"></Toast>
    <Toast ref="tcannel" :show="isshow" content="" cls="t-cannel"></Toast>
    <Toast ref="twarn" :show="isshow" content="" cls="t-warn"></Toast>
    <Toast ref="ttext" :show="isshow" content="" cls="t-text"></Toast>
    <Toast ref="tloading" :show="isshow" content="" cls="t-loading"></Toast>
  </page-content>
</div>
</template>
<script>
import { Button } from "@/components/button";

import { Header, hButton, Title } from "@/components/header";

import Content from "@/components/content";
import {
  Toast
  } from '@/components/toast'
/*
import toast from "@/components/toast";
*/
export default {
  components: {
    Toast,
    Header,
    Title,
    "page-content": Content,
    "header-button": hButton,
    "m-button": Button
  },
  name: "Button",
  data: () => ({
    customModal: false,
    isshow: false
  }),
  methods: {
    alertfn: function() {
      alert("alertFN");
    },
  /*
    openToast() {
      this.$refs.toast.open();
    }
    */
    openToast: function (s) {
      this.$refs[s].open()
    }
  }
};
</script>
<style lang="less" scoped>
.page-content {
  font-size: 12px;
  padding: 10px;
  p {
    padding: 5px;
  }
}
</style>



