<template>
<div class="page">
  <Header>
    <Title></Title>
    <header-button v-back-link></header-button>
  </Header>

  <page-content>
    <p>
      <m-button @click.stop.native="openAlertModal"></m-button>
    </p>
    <p>
      <m-button @click.stop.native="openConfirmModal"></m-button>
    </p>
    <p>
      <m-button @click.stop.native="openPromptModal"></m-button>
    </p>
    <p>
      <m-button @click.native="openCustomModal"></m-button>
    </p>
  </page-content>

  <modal ref="modal" :show="this.customModal" @open="modalOpen">
    <div slot="title"></div>
    <div slot="content"></div>
    <div slot="buttons" class="modal-buttons">
      <span class="modal-button modal-button-cancel" @click="closeCustomModal();"></span>
      <span class="modal-button" @click="selWx()"></span>
      <span class="modal-button modal-button-bold" @click="selZfb()"></span>
    </div>
  </modal>

  <alert ref="alertwx" title="" content=""></alert>
  <alert ref="alertzfb" title="" content=""></alert>

  <alert ref="alert" :on-ok="alertfn"></alert>
  <confirm ref="confirm" :on-ok="confirmfn"></confirm>
  <prompt ref="prompt" :on-ok="promptfn"></prompt>
</div>
</template>
<script>
import {
  Button
} from '@/components/button'

import {
  Header,
  hButton,
  Title
} from '@/components/header'

import Content from '@/components/content'

import {
  Modal,
  Alert,
  Confirm,
  Prompt
} from '@/components/modal'

export default {
  components: {
    Modal,
    Alert,
    Confirm,
    Prompt,
    Header,
    Title,
    'page-content': Content,
    'header-button': hButton,
    'm-button': Button
  },
  name: 'Button',
  data: () => ({
    customModal: false
  }),
  methods: {
    alertfn: function() {
      alert('alertFN')
    },
    modalOpen: function() {

    },
    closeCustomModal: function(){
      this.$refs.modal.close()
    },
    openCustomModal: function(event) {
      //this.customModal = true
      this.$refs.modal.open()
    },
  selWx: function(){
    this.$refs.modal.close()
    this.$refs.alertwx.open()
  },
  selZfb: function(){
    this.$refs.modal.close()
    this.$refs.alertzfb.open()
  },

    openAlertModal: function(event) {
      this.$refs.alert.open()
    },
    confirmfn: function() {
      alert('confirmFN')
    },
    openConfirmModal: function(event) {
      this.$refs.confirm.open()
    },
    promptfn: function(value){
      alert(value)
    },
    openPromptModal: function(event){
      this.$refs.prompt.open()
    }
  }
}
</script>
<style lang="less" scoped>
.page-content {
    font-size: 12px;
    padding: 10px;
    p {
        padding: 5px;
    }
}
</style>



