<script>
import Page from '@/components/page'
import Content from '@/components/content'
import {
  mapActions,
  mapGetters,
  mapState
} from 'vuex'

import {
  Header,
  hButton,
  Title
} from '@/components/header'

import {
  Alert
} from '@/components/modal'

export default {
  name: 'Home',
  data: function () {
    return {
      menulist: [
        {"name":"Flex"},
        {"name":"Button"},
        {"name":"ButtonGroup"},
        {"name":"Grid"},
        {"name":"List"},
        {"name":"formValidate"},
        {"name":"Form"},
        {"name":"Modal"},
        {"name":"Popup"},
        {"name":"Navbar"},
        {"name":"Steps"},
        {"name":"Toast"},
        {"name":"Slider"},
        {"name":"Scroll"},
        {"name":"SwiperPic"},
        {"name":"Toptip"},
        {"name":"Tabar"},
        {"name":"SlideDel"},
        {"name":"Count"},
        {"name":"Actionsheet"},
        {"name":"Select"},
        {"name":"datetimePicker"}

      ]
    }
  },
  watch: {
    count1: function() {
      console.log(`watch: ${this.count1}`)
    },

    // list: function() {
    //   //console.log(`watch: ${this.list}`)
    // }
  },

